import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';
import linkState from 'linkstate';
import { Text, Localizer, withText } from 'preact-i18n';

import Error from '../error';
import Message from '../message';

@observer
class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showToken: false,
            isCapsLock: false,
            email: this.props.email,
            code: '',
        };
    }

    handleEmailInput = async (e) => {
        const { userStore } = this.props.stores;
        const email = e.target.value;
        this.setState({ email });
        clearTimeout(this.checkEmailTimer);
        this.checkEmailTimer = setTimeout(() => userStore.checkEmail(email), 1000);
    }

    updateCapsLockStatus = (isCapsLock) => {
        this.setState({
            isCapsLock,
        });
    }

    sendLoginLink = () => {
        const { email = '' } = this.state;
        const { userStore } = this.props.stores;
        userStore.sendLoginLink(email);
    }

    validateCode = () => {
        const { email, code } = this.state;
        const { userStore } = this.props.stores;
        userStore.validateLoginCode(email, code);
    }

    resetLoginLinkMessages = () => {
        const { userStore } = this.props.stores;
        userStore.resetLoginLinkMessages();
    }

    resetLoginMessages = () => {
        const { userStore } = this.props.stores;
        userStore.resetLoginMessages();
    }

    resetEmailSent = () => {
        const { userStore } = this.props.stores;
        userStore.updateObjectField('loginLink', 'emailSent', false);
    }

    newUser = () => {
        const { email = '' } = this.state;
        const { routeLoginNew = `/register/${email}` } = this.props;
        route(routeLoginNew);
    }

    login = () => {
        const { email = '' } = this.state;
        const { routeLogin = `/${email}` } = this.props;
        route(routeLogin);
    }

    changeLanguage = (e) => {
        const lang = e.target.value;
		const { appState } = this.props.stores;
        appState.setLanguage(lang);
    }

    componentDidMount() {
        const { userStore, appState } = this.props.stores;
        const { language = 'en' } = appState;
        if (this.props.email) {
            clearTimeout(this.checkEmailTimer);
            this.checkEmailTimer = setTimeout(() => userStore.checkEmail(this.props.email, language), 1000);
        }
    }

    render() {
        const { isCapsLock, email, code } = this.state;
        const { appState, userStore } = this.props.stores;
        const { showLogo = true } = this.props;
        const { language = 'en' } = appState;
        const { emailIsAvailable, loginLink, login } = userStore;
        const { emailSent, message, messageIcon, error, errorIcon } = loginLink;
        const {
            message: loginMessage, messageIcon: loginMessageIcon,
            error: loginError, errorIcon: loginErrorIcon
        } = login;

        const isValidEmail = util.validateEmail(email);
        const isOkToSendLink = isValidEmail && !emailIsAvailable;
        const isValidCode = util.validateCode(code, 100000, 999999);

        const darkmode = true;

        if (emailSent) {
            return (
                <div class='container-fluid'>
                    <div class='row'>
                        <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-md-6 offset-md-3 col-lg-4 offset-lg-4 pt-5'>
                            {showLogo && <>
                                <div class='d-flex justify-content-center mb-5 mt-5'>
                                    {darkmode ? <>
                                        <img src={`https://themusher.app/assets/logo-qpaws-dark.png`} height='100' />
                                    </> : <>
                                        <img src={`https://themusher.app/assets/logo-qpaws-light.png`} height='100' />
                                    </>}
                                </div>
                            </>}
                            {/* <div class='d-flex justify-content-center'>
                                <h5 class='mt-1 mb-5'><Text id='forgotten.forgotten'>Glemt passord</Text></h5>
                            </div> */}

                            <Error message={error} icon={errorIcon} remove={this.resetLoginLinkMessages} />
                            <Error message={loginError} icon={loginErrorIcon} remove={this.resetLoginMessages} />
                            <Message message={message} icon={messageIcon} remove={this.resetLoginLinkMessages}  />
                            <Message message={loginMessage} icon={loginMessageIcon} remove={this.resetLoginMessages}  />

                            <div class='form-group'>
                                {/* <label for='codeInput'><i class='fas fa-unlock-alt' /> <Text id='forgotten.one-time-code'>Engangskode for</Text> {email}</label> */}
                                <Localizer>
                                    <input type='text'
                                        class={`form-control form-control-lg ${code ? (isValidCode ? 'is-valid' : 'is-invalid') : ''}`}
                                        style='font-size: 40px; letter-spacing: 5px'
                                        id='codeInput'
                                        onInput={linkState(this, 'code')}
                                        value={code}
                                        placeholder={<Text id='forgotten.one-time-code'>Engangskode</Text>}
                                    />
                                </Localizer>
                                {code && !isValidCode && <div class="invalid-feedback text-white" style={'display: block;'}>
                                    <Text id='forgotten.otp-not-valid'>Hmm, koden ser ikke helt riktig ut. Kan det vÃ¦re en skrivefeil?</Text>
                                </div>}
                                {code && isValidCode && !emailIsAvailable && <div class="invalid-feedback text-white" style={'display: block;'}>
                                    <Text id='forgotten.otp-valid'>Koden ser bra ut.</Text>
                                </div>}
                            </div>

                            <button class={`btn btn-block btn-lg ${isValidCode ? 'btn-primary' : 'btn-primary'}`} type='button'
                                onClick={this.validateCode}
                                disabled={!isValidCode}
                            >
                                <i class='far fa-paper-plane mr-2' />
                                <Text id='forgotten.login-with-otp'>Logg meg inn med koden</Text>
                            </button>


                            <small class='form-text text-muted mt-5 text-center'>
                                <button class='btn btn-small btn-link text-white'
                                    onClick={this.login}
                                >
                                    <Text id='forgotten.goto-login'>Jeg husker passordet. GÃ¥ til logg inn.</Text>
                                </button>
                            </small>

                            <small class='form-text text-muted mt-2 text-center'>
                                <button class='btn btn-small btn-link text-white'
                                    onClick={this.resetEmailSent}
                                >
                                    <Text id='forgotten.send-new-otp'>Jeg trenger en ny kode.</Text>
                                </button>
                            </small>

                        </div>
                    </div>
                </div>
            );
        }

        return (
            <div class='container-fluid'>
                <div class='row'>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-md-6 offset-md-3 col-lg-4 offset-lg-4 pt-5'>
                        {showLogo && <>
                            <div class='d-flex justify-content-center mb-5 mt-5'>
                                {darkmode ? <>
                                    <img src={`https://themusher.app/assets/logo-qpaws-dark.png`} height='100' />
                                </> : <>
                                    <img src={`https://themusher.app/assets/logo-qpaws-light.png`} height='100' />
                                </>}
                            </div>
                        </>}
                        {/* <div class='d-flex justify-content-center'>
                            <h5 class='mt-1 mb-5'><Text id='forgotten.forgotten'>Glemt passord</Text></h5>
                        </div> */}

                        <Error message={error} icon={errorIcon} remove={this.resetLoginLinkMessages} />
                        <Message message={message} icon={messageIcon} remove={this.resetLoginLinkMessages}  />

                        <div class='form-group'>
                            {/* <label for='emailInput'><i class='far fa-envelope' /> <Text id='forgotten.email'>E-post</Text></label> */}
                            <Localizer>
                                <input type='email'
                                    class={`form-control form-control-lg ${email ? (isValidEmail && !emailIsAvailable ? 'is-valid' : 'is-invalid') : ''}`}
                                    id='emailInput'
                                    onInput={this.handleEmailInput}
                                    value={email}
                                    onKeypress={(e) => { util.isCapsLock(e, this.updateCapsLockStatus); }}
                                    placeholder={<Text id='forgotten.email'>E-post</Text>}
                                />
                            </Localizer>
                            {email && isValidEmail && emailIsAvailable && <div class='invalid-feedback text-white' style={`display: block;`}>
                                <Text id='forgotten.email-not-found'>E-posten finnes ikke.</Text> <a href={`/register/${email}`}><Text id='forgotten.register'>registrere deg</Text></a> ðŸ˜ƒ
                            </div>}
                            {email && !isValidEmail && <div class="invalid-feedback text-white" style={'display: block;'}>
                                <Text id='forgotten.email-not-valid'>Hmm, e-posten ser ikke helt riktig ut. Kan det vÃ¦re en skrivefeil?</Text>
                            </div>}
                            {email && isValidEmail && !emailIsAvailable && <div class="invalid-feedback text-white" style={'display: block;'}>
                                <Text id='forgotten.email-looks-good'>E-post ser bra ut.</Text>
                            </div>}
                        </div>


                        {isCapsLock && <div class='alert alert-danger' role='alert'>
                            <Text id='forgotten.caps-lock-is-on'>CAPS LOCK er pÃ¥!</Text>
                        </div>}

                        <button class={`btn btn-block btn-lg ${isOkToSendLink ? 'btn-primary' : 'btn-primary'}`} type='button'
                            onClick={this.sendLoginLink}
                            disabled={!isOkToSendLink}
                        >
                            <i class='far fa-paper-plane mr-2' />
                            <Text id='forgotten.send-reset-link'>Send meg en reset link</Text>
                        </button>

                        <small class='form-text text-muted mt-5 text-center'>
                            <button class='btn btn-small btn-link text-white'
                                onClick={this.login}
                            >
                                <Text id='forgotten.goto-login'>Jeg husker passordet. GÃ¥ til logg inn.</Text>
                            </button>
                        </small>

                    </div>
                </div>
            </div>
        );
    }
}

export default Login;
